const getNavList = lng => {
  return lng === 'ua'
    ? [
        { name: '', href: '#author' },
        { name: ' ', href: '#aboutMatrix' },
        { name: '', href: '#4methods' },
        { name: '', href: '#feedback' },
        { name: 'FAQ', href: '#peopleAsk' },
        { name: '', href: '/matrix/calculator' },
      ]
    : [
        { name: '', href: '#author' },
        { name: ' ', href: '#aboutMatrix' },
        { name: '', href: '#4methods' },
        { name: '', href: '#feedback' },
        { name: 'FAQ', href: '#peopleAsk' },
        { name: '', href: '/matrix/calculator' },
      ];
};

export default getNavList;
